import axios from 'axios'

//获取热门搜索得数据
export function getHotSearch(latitude,longitude){
	let url = 'static/1.json'
	let params = {latitude,longitude}
	return new Promise((resolve,reject)=>{
		axios.get(url,{params}).then((res)=>{
			resolve(res)
		}).catch((err)=>{
			reject(err)
		})
	})
}

//获取热门食品分类
export function getEntries(latitude,longitude){
	let url = 'static/entries.json'
	let params = {latitude,longitude,'templates[]':'main_templates'}
	return new Promise((resolve,reject)=>{
		axios.get(url,{params}).then((res)=>{
			resolve(res)
		}).catch((res)=>{
			reject(err)
		})
	})
}
//获取推荐商家数据
export function getRestaruants(latitude,longitude,offset,limit){
	let url = 'static/resteraunt.json'
	let params = {latitude,longitude,offset,limit,'extras[]':'activites','terminal':'h5'}
	return new Promise((resolve,reject) => {
		axios.get(url,{params})
		.then((res) => {
			resolve(res)
		})
		.catch((err) => {
			reject(err)
		})
	})
}


