<template>
	<div>
		<header>
			<div class="location">
				<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 26 31" class="icon-location">
						<path fill="#FFF" fill-rule="evenodd" d="M22.116 22.601c-2.329 2.804-7.669 7.827-7.669 7.827-.799.762-2.094.763-2.897-.008 0 0-5.26-4.97-7.643-7.796C1.524 19.8 0 16.89 0 13.194 0 5.908 5.82 0 13 0s13 5.907 13 13.195c0 3.682-1.554 6.602-3.884 9.406zM18 13a5 5 0 1 0-10 0 5 5 0 0 0 10 0z"></path>
					</svg> 
					<span class="icon-arrow">保定市人民政府</span>
					<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 14 8" class="icon-location">
						<path fill="#FFF" fill-rule="evenodd" d="M5.588 6.588c.78.78 2.04.784 2.824 0l5.176-5.176c.78-.78.517-1.412-.582-1.412H.994C-.107 0-.372.628.412 1.412l5.176 5.176z"></path>
					</svg>				
			</div>
		</header>
		<section class="search">
			<div class="button">
				<a href="">
					<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 16 16"><path fill-opacity=".38" d="M14.778 13.732a.739.739 0 1 1-1.056 1.036l-2.515-2.565a.864.864 0 0 1-.01-1.206 4.894 4.894 0 0 0 1.357-3.651c-.126-2.492-2.156-4.52-4.648-4.647a4.911 4.911 0 0 0-5.16 5.163c.126 2.475 2.13 4.496 4.605 4.642.451.026.896-.008 1.326-.1a.739.739 0 0 1 .308 1.446c-.56.12-1.137.164-1.72.13-3.227-.19-5.83-2.815-5.995-6.042a6.39 6.39 0 0 1 6.71-6.715c3.25.165 5.884 2.8 6.05 6.048a6.37 6.37 0 0 1-1.374 4.3l2.12 2.161z"></path></svg>
					<span>搜索饿了么商家、商品名称</span>
				</a>
			</div>
		</section>
		<section class="hot-search">
			<ul>
				<li v-for="(item,index) in hotsearch" :key="index">{{item.search_word}}</li>
			</ul>
		</section>
		<section class="entries">
			<mt-swipe :auto="4000">
				<mt-swipe-item v-for="(item,index) in entries" :key="index">
					<a href="" v-for="(item2,index2) in item" :key="index2">
						<img :src="item2.image_hash | imgUrl" alt="">
						<span>{{item2.name}}</span>
					</a>
				</mt-swipe-item>
			</mt-swipe>
		</section>
		<!-- 推荐商家 -->
		<section>
				<div class="shop-title">推荐商家</div>
		</section>
		<!-- 商家列表 -->
		<section class="shop-list" v-for="(item,index) in restaurants" :key="index">
			<section class="wrapper">
				<div class="shop-info">
					<div class="shop-logo">
						<img :src="item.restaurant.image_path|imgUrl">
					</div>
					<div class="shop-main">
						<div class="title">
							<h3>{{item.restaurant.name}}</h3>
							<span class="supportWrap">···</span>
						</div>
						<div class="rating">
							<div class="ratingWrap">
								<elm-star :score=item.restaurant.rating :size="24"></elm-star>
								<div class="list">
									<span>{{item.restaurant.rating}}</span>
									<span>月售{{item.restaurant.recent_order_num}}单</span>
								</div>
								
							</div>
							<div class="deliver-mode">
								<span v-for="(item2,index2) in item.delivery_mode" :key="index2">蜂鸟专送</span>
							</div>
						</div>
						<div class="fee">
							<div class="money">
								<span>￥15起送</span>
								<span>优惠配送费￥3</span>
							</div>
							<div class="distance">
									<span>{{item.restaurant.distance/1000}}km</span>
									<span>{{item.restaurant.order_lead_time}}分钟</span>
							</div>
						</div>
						<div class="shop-activity">
							<div class="tag-top">
								<div class="tag-line">
									<img src="" alt="">
									<span>口碑人气好店</span>
								</div>
								<div class="activityBtn">
									<span>{{item.restaurant.activities.length}}活动</span>
									<img src="" alt="">
								</div>
							</div>
							
							<div class="activities" v-for="(item3,index3) in item.restaurant.activities" :key="index3">
								<span class="discount">减</span>
								<span>{{item3.tips}}</span>
							</div>
							
						</div>
					</div>
				</div>
			</section>
		</section>
		<elm-tabbar></elm-tabbar>
	</div>

</template>
<script>
	import Tabbar from '@/components/footer/Tabbar';
	import {getHotSearch,getEntries,getRestaruants} from '@/api/msite'
	import _ from 'lodash'
	import { Swipe, SwipeItem } from 'mint-ui';

	import 'mint-ui/lib/style.css'
	import 'mint-ui/lib/index.js'
	import Vue from 'vue'
	Vue.component(Swipe.name, Swipe);
	Vue.component(SwipeItem.name, SwipeItem);

	import Star from '@/components/star/Star.vue';

	export default{
		components:{
			'elm-tabbar':Tabbar,
			'elm-star':Star
		},
		data(){
			return{
				hotsearch:[],
				entries:[],
				restaurants:[],
				page:1,
				limit:10
			}
		},
		created(){
			getHotSearch(40.1144418,116.3579348)
			.then((res)=>{
				this.hotsearch = res.data
				//console.log(this.hotsearch)
			})
			.then(()=>{
				getEntries(40.1144418,116.3579348)
				.then((res)=>{
					//console.log(res.data)
					this.entries = _.chunk(res.data[0].entries,8)
					console.log(this.entries)
				})
			})
			.then(()=>{
				let offset = (this.page - 1)*this.limit;
				getRestaruants(40.1144418,116.3579348,offset,this.limit).then((res) => {
					console.log(res.data.items)
					this.restaurants = res.data.items;
				})
			})
			.catch((err)=>{
				console.log(err)
			})
		},
		filters:{
			imgUrl(src){
				let url = "https://fuss10.elemecdn.com/";
				url += src.slice(0,1) + '/' +src.slice(1,3) + '/' +src.slice(3);
				let reg = /jpeg|jpg|png$/;
				let result = url.match(reg);
				console.log(result[0])
				url += "."+result[0]+"?imageMogr/format/webp/thumbnail/!90x90r/gravity/Center/crop/90x90/";
				return url;
			}
		}
	}
</script>
<style lang="less" scoped>
	header{
		background-image: linear-gradient(90deg,#0af,#0085ff);
		height: 1.333333rem;
		display: flex;
		align-items: center;
		padding-left: 0.346667rem;
		
		.location{
			display: flex;
			align-items: center;
			.icon-location{
				width: 0.346667rem;
				height: 0.413333rem;
			}
			.icon-arrow{
				font-size: 0.453333rem;
				max-width: 80%;
			    white-space: nowrap;
			    overflow: hidden;
			    text-overflow: ellipsis;
			}
		}
	}
	.search{
		position: sticky;
			top: 0;
			z-index: 9999;
		.button{
		   background-image: linear-gradient(90deg,#0af,#0085ff);
		   
		   padding: .2rem .373333rem;
		   margin: -.013333rem 0;
		   a{
		   	display: flex;
    		width: 100%;
    		height: .96rem;
    		justify-content: center;
    		align-items: center;
    		border-radius: .026667rem;
    		background: #fff;
		    color: #999;
		    font-size: .373333rem;
		    outline: 0;
		    color: inherit;
		    text-decoration: none;
		    svg{
		    	display: inline-block;
   	 			width: .426667rem;
   	 			height: .426667rem;
   	 			margin-right: .133333rem;
		    }
		   }
		}
	}
	.hot-search{
		height: 1.12rem;
		background-image: linear-gradient(90deg,#0af,#0085ff);
		width: 100%;
		text-align: center;
		line-height: 0.533333rem;
		
		ul{
			margin-top: 0rem;
			overflow-x: auto;
			white-space: nowrap;
			
			&::-webkit-scrollbar{
				display: none;
			}
			li{
				font-size: 0.32rem;
				display: inline-block;
				color: #fff;
				&:not(:last-child){
				margin-right: 0.48rem;
				}
			}
		}
	}
	.entries{
		height: 4.533333rem;
		z-index: -999;
		a{
			
			text-decoration: none;
			display: flex;
			flex-direction: column;
			align-items: center;
			width: 25%;
			float: left;
			margin-top: 0.373333rem;
			img{
				width: 0.853333rem;
				height: 0.853333rem;
			}
			span{
				font-size: 0.32rem;
				color: #666;
				margin-top: 0.266667rem;
			}
		}
	}
	.shop-title{
		display: flex;	
		align-items: center;
		justify-content: center;
		font-size: .5rem;
		&:after,&:before{
			content:"";
			display: block;
			width: 0.533333rem;
			height: 0.026667rem;
			background: #999;
			
		}
		&:before{
			margin-right: 0.346667rem;
		}
		&:after{
			margin-left: 0.346667rem;
		}
	}
	// 商家列表
	.shop-list{
		.shop-info{
			display: flex;	
			align-items: stretch;
			margin-top: .625rem;
			.shop-main{
				width: 100%;
				display: flex;
				flex-direction:column;
				margin-right: .3125rem;
				.title{
					display: flex;
					justify-content:space-between;
					font-size: 0.4rem;
				}
				.rating{
					display: flex;
					justify-content: space-between;
					height: .625rem;
					font-size:0.293333rem;
					margin-top: .3125rem;
					.ratingWrap{
						display: flex;
						justify-content: flex-start;
						.list{
							margin-left: .625rem;
							margin-top: -.03125rem
						}
					}
					
				}
				.fee{
					display: flex;
					justify-content: space-between;
					font-size: .293333rem;
					margin-top: .3125rem;
					
				}
				.shop-activity{
					margin-top: .3125rem;
					.tag-top{
						display: flex;
						justify-content: space-between;
						.tag-line,.activityBtn{
							font-size: .293333rem;
						}
						
					}
				}
				.activities{
					margin-top: .3125rem;
					font-size: .293333rem;
					.discount{
						color: #fff;
						background-color: rgb(240, 115, 115);
					}
				}
			}
		}
	}
</style>


