<template>
	<footer>
		<div>
			<a @click="direc('/msite')">
				<svg class="index-footerTabIcon_1EbB8wS">
					<use xlink:href="#h5-component-index"></use>
				</svg>
			<span>首页</span>
    		</a>
    		<a @click="direc('/discover')">
    			<svg class="index-footerTabIcon_1EbB8wS">
    				<use xlink:href="#h5-component-discoverRegular">
    				</use>
    			</svg>
			<span>发现</span>
    		</a>
    		<a @click="direc('/order')">
    			<svg class="index-footerTabIcon_1EbB8wS">
    				<use xlink:href="#h5-component-orderRegular"></use>
    			</svg>
			<span>订单</span>
    		</a>
    		<a @click="direc('/profile')">
    			<svg class="index-footerTabIcon_1EbB8wS">
    				<use xlink:href="#h5-component-profileRegular"></use>
    			</svg>
			<span>我的</span>
    		</a>
		</div>
	</footer>
</template>
<script type="text/javascript">
	export default{
		methods:{
			direc(path){
				this.$router.push({path:path})
			}
		}
	}
</script>
<style lang="less" scoped>
	footer{
		width:100%;
		height: 1.6rem;
		position: fixed;
	    left: 0;
	    right: 0;
	    bottom: 0;
	    line-height: 1;
		box-shadow: 0 -0.266667vw 0.533333vw rgba(0,0,0,.1);
		background-color: #fff;
	    div{
	    	width: 100%;
	    	height: 1.333333rem;
	    	display: flex;
	    	justify-content: space-around;
	    	align-items: center;
	    	svg{
	
	    		width: 0.533333rem;
	    		height: 0.533333rem;
	    	}
	    	span{

	    		font-size: 0.266667rem;
	    		display: block;
	    	}
	    	a{
	    		width: 100%;
	    		text-align: center;
	    	}
	    }
	}
</style>


